.PaymentRenewals {
  height: 100vh;
  font-family: Roboto;
  overflow-x: hidden;
}

.PaymentRenewals_Main {
  width: 90%;
  margin: 5% auto 5% 13%;
}

.Detail {
  display: grid;
  grid-template-columns: 60% 40%;
}

.Status_Title {
  margin: 0 0 1% 0;
  opacity: 0.5;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #24272c;
}

.Detail {
  display: grid;
  grid-template-columns: 50% 33% 10%;
  padding: 14px 0 20px 0;
  list-style: none;
  position: relative;
}

.Payment_Grid_Detail h1 {
  font-size: 16px;
  font-weight: 500;
  color: #24272c;
}

.Payment_Grid_Detail p {
  font-size: 12px;
  color: #e02020;
  margin-top: 2%;
}

.Payment_Grid_Amount h1 {
  font-size: 14px;
  font-weight: 500;
  text-align: right;
  color: #24272c;
  opacity: 0.5;
}

.Payment_Grid_Amount p {
  text-align: right;
  font-size: 12px;
  color: #566171;
  opacity: 0.5;
}

.Renew_Btn {
  margin-top: 3% !important;
  margin-left: 0 !important;
  height: 32px !important;
  padding: 8px 15px !important;
  border-radius: 4px !important;
  font-size: 14px !important;
  font-weight: 500 !important;
  text-align: center !important;
  color: #ffffff !important;
  box-shadow: none !important;
}
.Options {
  position: relative;
}

.Options img {
  opacity: 0.5;
  width: 14px;
  margin: 2% auto;
  display: block;
}

.Toggler {
  transition: all 0.1s ease-in;
  width: 130px;
  z-index: 11;

  border-radius: 4px;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.08);
  background-color: #ffffff;
  position: absolute;
  transform: translate(-80%, -100%);
  pointer-events: all;
  opacity: 1;
}

.Toggler p {
  color: black;
  font-size: 14px;
  padding: 15px 0px 15px 15px;
}

.Toggler p:hover {
  /* background-color: #24272c;
  color: white; */
}

.Toggler p:nth-child(2) {
  margin-top: 15%;
}

.Hide {
  transform: translate(-40%, -70%);
  pointer-events: none;
  opacity: 0;
}

.ActiveBusiness {
  position: relative;
  padding-bottom: 3%;
}

.ActiveBusiness::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 90%;
  height: 1px;
  background-color: #24272c;
  opacity: 0.15;
}

.InactiveBusiness {
  margin-top: 6%;
}
